import React from 'react'
import Pdf from './files/Charles_Curtis_Resume.pdf'

const Resume = () => {
  return (
    <div className="resume p-2">
      <h3>Resume</h3>
      <a className="btn btn-lg btn-success" href={Pdf}>View/Download Resume</a>
    </div>
  )
}

export default Resume



