import React from 'react'
import { Link } from 'react-scroll'
import { Navbar, Nav } from 'react-bootstrap'

const Header = () => (
  <Navbar expand="md" className="sticky-top m-0 header">
    <Navbar.Brand><h3><a href="#" style={{ 'color': 'black', 'textDecoration': 'none' }}>Charles Curtis</a></h3></Navbar.Brand>
    <Navbar.Toggle aria-controls="basic-navbar-nav" />
    <Navbar.Collapse id="basic-navbar-nav">
      <Nav className="ml-auto text-center">
        <Link
          to='home'
          spy={true}
          smooth={true}
          offset={-120}
          duration={500}
          className="scroll-nav"
          style={{ textDecoration: 'none' }}
        >Home</Link>
        <Link
          to='projects'
          spy={true}
          smooth={true}
          offset={-120}
          duration={500}
          className="scroll-nav"
          style={{ textDecoration: 'none' }}
        >Projects</Link>
        <Link
          to='about'
          spy={true}
          smooth={true}
          offset={-120}
          duration={500}
          className="scroll-nav"
          style={{ textDecoration: 'none' }}
        >About</Link>
        <Link
          to='resume'
          spy={true}
          smooth={true}
          offset={-120}
          duration={500}
          className="scroll-nav"
          style={{ textDecoration: 'none' }}
        >Resume</Link>
        <Link
          to='contact'
          spy={true}
          smooth={true}
          offset={-120}
          duration={500}
          className="scroll-nav"
          style={{ textDecoration: 'none' }}
        >Contact</Link>
      </Nav>
    </Navbar.Collapse>
  </Navbar>
)

export default Header



